<template>
    <div class="icon_wrapper">
        <div class="icon" @mouseenter="isHover = true" @mouseleave="isHover = false">
            <img class="icon__active" :src="item[type]" alt="">
            <div v-if="isHover" @click="startPlay">
                <img class="icon__pause_btn" :src="`${$router.options.base}media/main/pause.svg`" alt="pause" v-if="item.clicked">
                <img class="icon__play_btn" :src="`${$router.options.base}media/main/play.svg`" alt="play" v-else>
            </div>
        </div>
        <audio
            ref="audio"
            :src="item.audio" class="icon__audio"/>
    </div>
</template>

<script>
export default {
    props: ['item', 'allActive'],
    data() {
        return {
            isHover: false,
        }
    },
    computed: {
        type() {
            if(this.allActive) return 'active'
            return this.item.clicked ? 'play' : 'stop'
        },
    },
    methods: {
        async startPlay() {
            await this.$emit('onClick')
            if(this.item.clicked) this.$refs.audio.play()
            else this.$refs.audio.pause()
        }
    }
}
</script>

