.icon {
    width: 250px;
    cursor: pointer;
    position: relative;
    &__play_btn, &__pause_btn {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    img {
        max-width: 100%;
    }
    &__audio {
        visibility: hidden;
    }
    &_wrapper {
        width: calc(50% - 7.2%);
        height: 250px;
        margin-bottom: 14.7%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        &:first-of-type, &:nth-of-type(2) {
            margin-top: 14.7%;
        } 
        &:nth-of-type(2n) {
            justify-content: flex-start;
        }
    }
}
</style>
